/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.edge-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
					}
				}
			}
		}
	}
}

@include phone-landscape {
	
	.edge-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}

			thead {

				tr {

					th {
						font-size: 14px;
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 160px;
						margin: 0 10px 20px 0;
					}
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
			}
		}

		.edge-content .edge-quantity-buttons .edge-quantity-input, div.woocommerce .edge-quantity-buttons {
			.edge-quantity-input,
			.edge-quantity-plus,
			.edge-quantity-minus {
				font-size: 12px;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */