/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-portrait) {

    .edge-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 40px 25px;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .edge-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
            }


            .woocommerce-Button,
            .button {
            	float: none;
                position: relative!important;
                display: block;
                text-align: center;
                margin-bottom: 15px!important;
                left: auto;
                right: auto;
                top: auto;
                @include edgeTransform( none );
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */