/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@include laptop-landscape-mac {

  .edge-woo-single-page {

    .price ins .amount {
      font-size: 18px;
    }

    ul.products>.product .price del {
      font-size: 14px;
    }

  }
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .edge-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .edge-woo-single-page {
        
        .edge-single-product-content {
            
            .images,
            .edge-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .edge-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {

                  &:first-child {
                    a {
                      padding-left: 0;
                    }
                  }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .edge-woo-single-page {
	
	    &.edge-woo-single-thumb-on-left-side {
		
		    .edge-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .edge-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }
    }

    .edge-woocommerce-page .edge-content .variations .reset_variations {
        display: block;
        margin: 10px 0 0;
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */